<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Resources</ion-title>
        <ion-input placeholder="Filter" @ionChange="handleFilter" v-model="filter"></ion-input>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-header collapse="condense">
        <ion-toolbar>
          <ion-title size="large">Resources</ion-title>
        </ion-toolbar>
      </ion-header>
      <ion-list>
        <ion-item v-for="item in items" :key="item">
          <ResourceCard v-if="item.displayed" :id="item.id" :previewImg="'/'+backendUrl+'/resourceEntries/'+item.id+'/previewImages/'+item.previewImageId" :description="item.description" :title="item.title" />
        </ion-item>
      </ion-list>

      <ion-infinite-scroll
        @ionInfinite="loadData($event)" 
        threshold="100px" 
        id="infinite-scroll"
        :disabled="isDisabled"
      >
        <ion-infinite-scroll-content
          loading-spinner="bubbles"
          loading-text="Loading more data...">
        </ion-infinite-scroll-content>
      </ion-infinite-scroll>
    </ion-content>
  </ion-page>
</template>

<script>
import { IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonInfiniteScroll, IonInfiniteScrollContent, IonItem, IonList, IonInput } from '@ionic/vue';
import ResourceCard from '@/components/cards/ResourceCard.vue';
import axios from 'axios';
import { ref } from 'vue';

export default  {
  name: 'Tab1',
  components: { ResourceCard, IonHeader, IonToolbar, IonTitle, IonContent, IonPage,  IonInfiniteScroll, IonInfiniteScrollContent, IonItem, IonList, IonInput },
  setup() {
    const isDisabled = ref(false);
    const toggleInfiniteScroll = () => {
      isDisabled.value = !isDisabled.value;
    }

    const loadData = () => {
      isDisabled.value = true;
    }

    return {
      isDisabled,
      toggleInfiniteScroll,
      loadData
    }
  },
  methods: {
     async handleFilter() {
        this.items.forEach(x => {
          if (x.title.toLowerCase().indexOf(this.filter.toLowerCase()) != -1 || x.description.toLowerCase().indexOf(this.filter.toLowerCase()) != -1){
            x.displayed = true;
          }else{
            x.displayed = false;
          }
        });
      },
  },
  data: () => ({
    items: [],
    filter: '',
    backendUrl: process.env.VUE_APP_API_URL
  }),
  created: function(){
    axios.get(process.env.VUE_APP_API_URL+'/resourceEntries').then((response) => {
      response.data.forEach(element => {
        const item = {};
        item.title = element.title;
        item.description = element.description;
        item.previewImageId = element.images[0]?.id;
        item.id = element.id;
        item.displayed = true;
        this.items.push(item);
      });
    });
  },
}
</script>